.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 23px;
}

.checkoutProduct__info {
    padding-left: 20px
}

.checkoutProduct__info > button {
    background: #4beaf0;
    border: 1px solid;
    margin-top: 10px;
    padding: 12px;
    
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.checkoutProduct__image {
    object-fit: contain;
    width: 100px;
    height: 180px;
  }

.checkoutProduct__rating {
    display: flex;
  }

.checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
}
@media screen and (max-width: 670px){
    .checkoutProduct {
   
    margin-left: 13px;
}
.checkoutProduct__title{
    text-align: justify;
    margin-right: 9px;
    font-weight: 100;
}
}