.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 500px;
  height: 130px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal > button {
   background: #4bcff0;
    border-radius: 2px;
    width: 100%;
     height: 32px;
    border: 1px solid;
    font-weight: bolder;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
 @media screen and (max-width: 670px) {
    .subtotal{
      width: 280px;
      margin: 5px ;
      justify-content: center;
      
    }
    
  }